
*{
    margin: 0;
    padding:0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body{
    font-family: 'open sans';
}
header{     
  
    width: 100%;
    height: 122px;
    background:#1e2649;
}
td {
    text-align: center;
}
td >a{
    color:#fff;
    font-weight: 200;
    text-decoration: none;
    margin-right: 30px;
}
td >a:hover{
    text-decoration: underline;
    color:#4c62c4;
}
article{     
  
    width: 100%;
    height: 100%;
}
.cuerpo{
    display: grid;
    grid-template-areas:
    "img1 img2";         
    padding: 20px 20px 20px 20px;
    gap:20px;
}
.imagen-habitacion{  
    width: 100%;
    height:100%;     
    border-radius: 10px; 
    display: -webkit-box; 
    display: -ms-flexbox; 
    display: flex;
}
.contenedor:nth-of-type(1){
    grid-area: img1;
}
.contenedor:nth-of-type(2){
    grid-area: img2;
}
form{       
    background: #fff;
    border-radius:10px;
    -webkit-box-shadow: 0 0 6px 0 rgba(17, 27, 70, 0.8);
            box-shadow: 0 0 6px 0 rgba(17, 27, 70, 0.8); 
    margin: auto;
    text-align: left;    
    padding: 30px 30px 30px 30px;
}
form h1{
    text-align: center;
    color: #1e2649;
    padding: 0 0 20px 0;
}
label{
    color:#4c62c4;
    display: block;
    font-size: 18px;
    padding: 10px 0 10px 0;
}
input{
    color:#1e2649;
    font-size: 18px;
    display: block;
    padding: 10px 10px 10px 10px;
}
select{
    font-size: 18px;
    display: block;
    padding: 10px 10px 10px 10px;
}
button{
    background:#1e2649;
    display:block;
    width: 200px;
    height: 60px;
    border:none;
    color:#fff;
    border-radius: 4px;
    font-size: 20px;
    margin: 10px auto;
    cursor: pointer;
}
footer{
    background: #1b2138;
    padding: 10px 0 30px 0;
    margin: auto;
    overflow: hidden;
}
.contenedor-footer{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 80%;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    margin: auto;
    padding-bottom: 50px;
}
.contenedor-foo{
    text-align: center;
}
.contenedor-foo h4{
    color: #fff;
    border-bottom: 3px solid #fff;
    padding-bottom: 5px;
    margin-bottom: 10px;
}
.contenedor-foo p{
    color: #fff
}




#main_inicio{
    background-color: lightblue;
}

div{
-ms-flex-line-pack: center;
    align-content: center;
}
tr {
    display: table-row-group;
    -ms-flex-line-pack: left;
        align-content: left;
    border-color: inherit;
}































































































